  display: none; }

.List-Header {
  margin-bottom: 8px; }

.List-Item {
  margin-bottom: 10px; }

.List-Item-Header {
  font-size: 20px; }

.List-Item-Title {
  font-style: normal; }

.List-Item-Author {
  font-size: 20px;
  font-style: italic; }

.List-Item-Description {
  font-style: italic;
  font-size: 16px;
  margin: 0; }

.List-Item-Tags, .List-Item-Level {
  font-size: 13px; }

